import * as React from 'react';
import { Button, DeleteBetaNormal } from '@ali/we-design';
import { InlineToolbar } from '../Toolbar/inlineToolbar';

interface IProps {
  handleRemovePlaceholder: () => void;
  triggerNode: HTMLDivElement | null;
}

const PlaceholderInlineToolbar = (props: IProps) => {
  const onDeletePlaceholder = (
    e: React.MouseEvent<HTMLSpanElement> | React.TouchEvent<HTMLSpanElement>,
  ) => {
    e.preventDefault();
    e.stopPropagation();
    props.handleRemovePlaceholder();
  };

  const DelPlaceholderButton = () => {
    return <Button testid="ph-delete" onClick={onDeletePlaceholder} bordered={false} icon={<DeleteBetaNormal />} />;
  };

  const customToolButtons = {
    'placeholder.del': DelPlaceholderButton,
  };

  const layout = [{
    toolButtons: [{
      name: 'placeholder.del',
    }],
  }];
  return (
    <InlineToolbar
      visible
      triggerNode={props.triggerNode || undefined}
      customToolButtons={customToolButtons}
      inlineLayout={layout}
    />
  );
};

export default React.memo(PlaceholderInlineToolbar);
